<template>
	<view class="list_article" style="background-color: #fff;" >
		<navigator class="item_article" v-for="(o, i) in list" :key="i" :url="'/pages/article/details?' + vm.article_id + '=' + o[vm.article_id]">
			<view class="left">
				<image style="width:4rem;height: 4rem;" :src="$fullUrl(o[vm.img])" mode="scaleToFill"></image>
			</view>
			<view class="right_block">
				<view class="top">
					{{ o[vm.title] }}
				</view>
				<view class="time">{{ o[vm.create_time] }}</view>
				<view class="bottom">
					<text class="praise">{{ o[vm.praise_len] }}点赞</text>
					<text class="see">
						{{ o[vm.hits] }}点击
					</text>
				</view>
			</view>
		</navigator>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						img: "img",
						article_id: "article_id",
						title: "title",
						description: "description",
						title: "title",
						create_time: "create_time",
						content: "content",
						praise_len: "praise_len",
						hits: "hits"
					}
				}
			}
		}
	}
</script>

<style scoped>
	.list_article {
		padding: 0 1rem;
	}
	.list_article .item_article {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		padding: 0.5rem 0;
	}
	.list_article .item_article+.item_article{
		border-top: 1px solid #dbdbdb;
	}
	.list_article .item_article .left>image {
		position: relative;
		top: 50%;
		transform: translate(0, -50%);
		border-radius: 0.5rem;
	}

	.list_article .item_article .right_block {
		width: calc(100% - 5rem);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.list_article .top {
		font-size: 0.9rem;
	}


	.list_article .time {
		font-size: 0.6rem;
		color: var(--color_grey)
	}

	.list_article .bottom {
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		font-size: 0.5rem;
		color: var(--color_grey)
	}

	.list_article .see {
		margin-left: 1rem;
	}
</style>
